import Home from './reactgw/Home';
import Doc from './reactgw/Doc';
import Tuto from './reactgw/Tuto';
import Blog from './reactgw/Blog';
import Comm from './reactgw/Comm';
import Login from './reactgw/Login';
import Core from './reactgw/Core';
import Guide from './reactgw/Guide';
import Api from './reactgw/Api';
import Hooks from './reactgw/Hooks';



const routes=[
    {
        path:"/",
        exact:true,
        component:Home,
    },
    {
        path:"/doc",
        component:Doc,
        pri:true,
        routes:[
            {
                path:'/doc/core',
                component:Core,
            },
            {
                path:'/doc/guide',
                component:Guide,
            },
            {
                path:'/doc/api',
                component:Api,
            },
            {
                path:'/doc/hooks',
                component:Hooks,
            }
        ]
       
    },
    {
        path:"/tuto",
        component:Tuto,
        pri:true,
    },
    {
        path:"/comm",
        component:Comm,
       
    },
    {
        path:"/blog",
        component:Blog,
       
    },
    {
        path:"/login",
        component:Login,
       
    }
]

export default routes